import React, {Component} from 'react';
import ProjectItem from './project_item';


 class ProjectItems extends Component {
  render() {
    const projectItems = [
    {
      title: "ToneQuest",
      description: "is a guitar pedal review site and virtual pedalboard built with React and Ruby on Rails.",
      img_url: 'img/ToneQuest.png',
      url: 'https://tone-quest.herokuapp.com',
      repo: 'https://github.com/jsc215/tone-quest'
    },
    {
      title: "Rate-A-Hero",
      description: "is a Superhero review site built with React and Ruby on Rails.",
      img_url: 'img/RateAHero.png',
      url: 'https://rate-a-superhero.herokuapp.com',
      repo: 'https://github.com/apugliese36/rate-a-hero'
    },
    {
      title: "The Lyric Finder",
      description: "is a React app that uses the lyricsovh API to fetch song lyrics.",
      img_url: 'img/TheLyricFinder.png',
      url: 'https://TheLyricFinder.herokuapp.com',
      repo: 'https://github.com/jsc215/lyric-finder'
     },
    ];

    const items = projectItems.map(item => {
      return (
        <ProjectItem
          key={item.title}
          title={item.title}
          description={item.description}
          url={item.url}
          img_url={item.img_url}
          repo={item.repo}
        />
      );
    });

    return (
      <div className="container projects">
        <div className="row mono">
          {items}
        </div>
      </div>
    );
  }
}
export default ProjectItems;



